@if $enable-local-fonts == true {
    @font-face {
        font-family: 'Metropolis';
        font-style: normal;
        font-weight: 300;
        src: local('Metropolis-Light');
    }

    @font-face {
        font-family: 'Metropolis';
        font-style: italic;
        font-weight: 300;
        src: local('Metropolis-LightItalic');
    }

    @font-face {
        font-family: 'Metropolis';
        font-style: normal;
        font-weight: 600;
        src: local('Metropolis-SemiBold');
    }

    @font-face {
        font-family: 'Metropolis';
        font-style: italic;
        font-weight: 600;
        src: local('Metropolis-SemiBoldItalic');
    }

    @font-face {
        font-family: 'Metropolis';
        font-style: normal;
        font-weight: 800;
        src: local('Metropolis-ExtraBold');
    }

    @font-face {
        font-family: 'Metropolis';
        font-style: italic;
        font-weight: 800;
        src: local('Metropolis-ExtraBoldItalic');
    }
}
