//
// Variables
// --------------------------------------------------

// Variables directly translating Bootstrap variables
// -------------------------

//
// Mixins
// --------------------------------------------------
@mixin bootstrap-input-defaults($font-size: $s2bs-font-size-base) {
    @include box-shadow($s2bs-form-control-default-box-shadow);

    background-color: $s2bs-input-bg;
    border: $s2bs-input-border-width solid $s2bs-input-border;
    @include border-radius($s2bs-input-border-radius);

    color: $s2bs-input-color;
    font-size: $font-size;
}
@mixin validation-state-focus($color) {
    $shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 6px lighten($color, 20%);

    .select2-dropdown,
    .select2-selection {
        border-color: $color;
    }

    .select2-container--focus .select2-selection,
    .select2-container--open .select2-selection {
        @include box-shadow($shadow);

        border-color: darken($color, 10%);
    }

    &.select2-drop-active {
        border-color: darken($color, 10%);

        &.select2-drop.select2-drop-above {
            border-top-color: darken($color, 10%);
        }
    }
}
@mixin dropdown-arrow {
    .select2-selection--single {
        .select2-selection__arrow b {
            border-color: transparent transparent $s2bs-dropdown-arrow-color
                transparent;
            border-width: 0 $s2bs-caret-width-lg $s2bs-caret-width-lg
                $s2bs-caret-width-lg;
        }
    }
}

.select2-container--bootstrap4 {
    display: flex;
    flex-direction: column;
    min-width: 100%;
    > .selection {
        display: flex;
        flex: 1 1 100%;
        min-width: 100%;
        min-height: 100%;
    }
    .select2-selection {
        @include bootstrap-input-defaults;

        outline: 0;
        flex: 1 1 100%;
        min-width: 100%;
        min-height: 100%;

        &.form-control {
            @include border-radius($s2bs-border-radius-base);
        }
    }

    .select2-search--dropdown {
        .select2-search__field {
            @include bootstrap-input-defaults;
        }
    }

    .select2-search__field {
        outline: 0;

        &::-webkit-input-placeholder {
            color: $s2bs-input-color-placeholder;
        }

        &:-moz-placeholder {
            color: $s2bs-input-color-placeholder;
        }
        &::-moz-placeholder {
            color: $s2bs-input-color-placeholder;
            opacity: 1;
        }
        &:-ms-input-placeholder {
            color: $s2bs-input-color-placeholder;
        }
    }

    .select2-results__option {
        padding: $s2bs-padding-sm-y $s2bs-padding-x;
        font-size: $s2bs-font-size-base;

        &[role='group'] {
            padding: 0;
        }

        &[aria-disabled='true'] {
            color: $s2bs-dropdown-link-disabled-color;
            cursor: $s2bs-cursor-disabled;
        }

        &[aria-selected='true'] {
            background-color: $s2bs-dropdown-link-hover-bg;
            color: $s2bs-dropdown-link-hover-color;
        }
        &--highlighted[aria-selected] {
            background-color: $s2bs-dropdown-link-active-bg;
            color: $s2bs-dropdown-link-active-color;
        }

        .select2-results__option {
            padding: $s2bs-padding-sm-y $s2bs-padding-x;

            .select2-results__group {
                padding-left: 0;
            }

            .select2-results__option {
                margin-left: -$s2bs-padding-x;
                padding-left: $s2bs-padding-x * 2;

                .select2-results__option {
                    margin-left: -$s2bs-padding-x * 2;
                    padding-left: $s2bs-padding-x * 3;

                    .select2-results__option {
                        margin-left: -$s2bs-padding-x * 3;
                        padding-left: $s2bs-padding-x * 4;

                        .select2-results__option {
                            margin-left: -$s2bs-padding-x * 4;
                            padding-left: $s2bs-padding-x * 5;

                            .select2-results__option {
                                margin-left: -$s2bs-padding-x * 5;
                                padding-left: $s2bs-padding-x * 6;
                            }
                        }
                    }
                }
            }
        }
    }

    .select2-results__group {
        color: $s2bs-dropdown-header-color;
        display: block;
        padding: $s2bs-dropdown-header-padding-vertical $s2bs-padding-x;
        font-size: $s2bs-dropdown-header-font-size;
        line-height: $s2bs-line-height;
        white-space: nowrap;
    }

    &.select2-container--focus,
    &.select2-container--open {
        .select2-selection {
            @include box-shadow($s2bs-form-control-focus-box-shadow);
            @include transition($s2bs-form-control-transition);

            border-color: $s2bs-input-border-focus;
        }
    }

    &.select2-container--open {
        .select2-selection .select2-selection__arrow b {
            border-color: transparent transparent $s2bs-dropdown-arrow-color
                transparent;
            border-width: 0 $s2bs-caret-width-base $s2bs-caret-width-base
                $s2bs-caret-width-base;
        }

        &.select2-container--below {
            .select2-selection {
                @include border-bottom-radius(0);

                border-bottom-color: transparent;
            }
        }

        &.select2-container--above {
            .select2-selection {
                @include border-top-radius(0);

                border-top-color: transparent;
            }
        }
    }

    .select2-selection__clear {
        color: $s2bs-clear-selection-color;
        cursor: pointer;
        float: right;
        font-weight: bold;
        margin-right: 10px;

        &:hover {
            color: $s2bs-clear-selection-hover-color;
        }
    }

    &.select2-container--disabled {
        .select2-selection {
            border-color: $s2bs-input-border;
            @include box-shadow(none);
        }

        .select2-selection,
        .select2-search__field {
            cursor: $s2bs-cursor-disabled;
        }

        .select2-selection,
        .select2-selection--multiple .select2-selection__choice {
            background-color: $s2bs-input-bg-disabled;
        }

        .select2-selection__clear,
        .select2-selection--multiple .select2-selection__choice__remove {
            display: none;
        }
    }

    .select2-dropdown {
        @include box-shadow($s2bs-dropdown-box-shadow);

        border-color: $s2bs-input-border-focus;
        border-width: $s2bs-input-border-width;
        overflow-x: hidden;
        margin-top: -1px;

        &--above {
            @include box-shadow($s2bs-dropdown-box-shadow-above);

            margin-top: 1px;
        }
    }
    .select2-results > .select2-results__options {
        max-height: 200px;
        overflow-y: auto;
    }

    .select2-selection--single {
        height: $s2bs-input-height;
        line-height: $s2bs-line-height;
        padding: $s2bs-padding-y $s2bs-padding-x;

        .select2-selection__arrow {
            position: absolute;
            bottom: 0;
            right: $s2bs-padding-x;
            top: 0;
            width: $s2bs-caret-width-base;

            b {
                border-color: $s2bs-dropdown-arrow-color transparent transparent
                    transparent;
                border-style: solid;
                border-width: $s2bs-caret-width-base $s2bs-caret-width-base 0
                    $s2bs-caret-width-base;
                height: 0;
                left: 0;
                margin-left: -$s2bs-caret-width-base;
                margin-top: -$s2bs-caret-width-base/2;
                position: absolute;
                top: 50%;
                width: 0;
            }
        }

        .select2-selection__rendered {
            color: $s2bs-input-color;
            padding: 0;
        }

        .select2-selection__placeholder {
            color: $s2bs-input-color-placeholder;
        }
    }

    .select2-selection--multiple {
        min-height: $s2bs-input-height;
        padding: $s2bs-padding-y $s2bs-padding-x;
        height: auto;
        overflow: hidden;

        .select2-selection__rendered {
            box-sizing: border-box;
            display: inline-flex;
            flex-wrap: wrap;
            align-items: baseline;
            justify-content: flex-start;
            line-height: $s2bs-line-height;
            list-style: none;
            margin: 0 -#{$s2bs-input-tag-padding-x} -#{$s2bs-input-tag-padding-x}
                0;
            padding: 0;
            width: calc(100% + #{$s2bs-input-tag-padding-x});
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .select2-selection__placeholder {
            color: $s2bs-input-color-placeholder;
        }

        .select2-selection__choice {
            color: $s2bs-input-tag-color;
            font-size: $s2bs-input-tag-font-size;
            line-height: $s2bs-input-tag-line-height;
            background: $s2bs-input-tag-bg;
            border: $s2bs-input-border-width solid $s2bs-btn-default-border;
            @include border-radius($s2bs-selection-choice-border-radius);

            cursor: default;
            margin: 0 $s2bs-input-tag-padding-x $s2bs-input-tag-padding-x 0;
            padding: $s2bs-input-tag-padding-y $s2bs-input-tag-padding-x;
        }

        .select2-search--inline {
            .select2-search__field {
                background: transparent;
                padding: 0;
                line-height: $s2bs-line-height;
                margin-top: 0;
                min-width: 5em;
            }
        }

        .select2-selection__choice__remove {
            color: $s2bs-input-tag-color;
            cursor: pointer;
            display: inline-block;
            font-weight: bold;
            margin-right: $s2bs-input-tag-padding-x;

            &:hover {
                color: $s2bs-remove-choice-hover-color;
            }
        }
    }

    .select2-selection--single.input-sm,
    .input-group-sm & .select2-selection--single,
    .form-group-sm & .select2-selection--single {
        @include border-radius($s2bs-border-radius-sm);

        font-size: $s2bs-font-size-sm;
        height: $s2bs-input-height-sm;
        line-height: $s2bs-line-height;

        .select2-selection__arrow b {
            margin-left: -$s2bs-padding-sm-y;
        }
    }

    .select2-selection--multiple.input-sm,
    .input-group-sm & .select2-selection--multiple,
    .form-group-sm & .select2-selection--multiple {
        min-height: $s2bs-input-height-sm;
        @include border-radius($s2bs-border-radius-sm);

        .select2-selection__rendered {
            height: $custom-select-height-sm;
            padding-top: $input-padding-y-sm;
            padding-bottom: $input-padding-y-sm;
            padding-left: $input-padding-x-sm;
            font-size: $font-size-sm;
        }

        .select2-selection__choice {
            line-height: $s2bs-line-height;
            margin: 0 0 0 $s2bs-padding-sm-x/2;
            padding: 0 $s2bs-padding-sm-y;
        }

        .select2-search--inline .select2-search__field {
            padding: 0 $s2bs-padding-sm-x;
            font-size: $s2bs-font-size-sm;
            height: $s2bs-input-height-sm - 2;
            line-height: $s2bs-line-height;
        }
    }

    .select2-selection--single.input-lg,
    .input-group-lg & .select2-selection--single,
    .form-group-lg & .select2-selection--single {
        @include border-radius($s2bs-border-radius-lg);

        font-size: $s2bs-font-size-lg;
        height: $s2bs-input-height-lg;
        line-height: $s2bs-line-height;
        padding: $s2bs-padding-lg-y $s2bs-padding-lg-x + $s2bs-caret-width-lg *
            3 $s2bs-padding-lg-y $s2bs-padding-lg-x;

        .select2-selection__arrow {
            width: $s2bs-caret-width-lg;

            b {
                border-width: $s2bs-caret-width-lg $s2bs-caret-width-lg 0
                    $s2bs-caret-width-lg;
                margin-left: -$s2bs-caret-width-lg;
                margin-left: -$s2bs-padding-lg-y;
                margin-top: -$s2bs-caret-width-lg/2;
            }
        }
    }

    .select2-selection--multiple.input-lg,
    .input-group-lg & .select2-selection--multiple,
    .form-group-lg & .select2-selection--multiple {
        min-height: $s2bs-input-height-lg;
        @include border-radius($s2bs-border-radius-lg);

        .select2-selection__choice {
            font-size: $s2bs-font-size-lg;
            line-height: $s2bs-line-height;
            @include border-radius($s2bs-selection-choice-border-radius);

            margin: ($s2bs-padding-lg-y - 1) 0 0 $s2bs-padding-lg-x/2;
            padding: 0 $s2bs-padding-lg-y;
        }

        .select2-search--inline .select2-search__field {
            padding: 0 $s2bs-padding-lg-x;
            font-size: $s2bs-font-size-lg;
            height: $s2bs-input-height-lg - 2;
            line-height: $s2bs-line-height;
        }
    }

    .select2-selection.input-lg.select2-container--open {
        @include dropdown-arrow;
    }

    .input-group-lg & .select2-selection {
        &.select2-container--open {
            @include dropdown-arrow;
        }
    }

    &[dir='rtl'] {
        .select2-selection--single {
            padding-left: $s2bs-padding-x + $s2bs-caret-width-base * 3;
            padding-right: $s2bs-padding-x;

            .select2-selection__rendered {
                padding-right: 0;
                padding-left: 0;
                text-align: right;
            }

            .select2-selection__clear {
                float: left;
            }

            .select2-selection__arrow {
                left: $s2bs-padding-x;
                right: auto;

                b {
                    margin-left: 0;
                }
            }
        }

        .select2-selection--multiple {
            .select2-selection__choice,
            .select2-selection__placeholder,
            .select2-search--inline {
                float: right;
            }

            .select2-selection__choice {
                margin-left: 0;
                margin-right: $s2bs-padding-x/2;
            }

            .select2-selection__choice__remove {
                margin-left: 2px;
                margin-right: auto;
            }
        }
    }
}

.has-warning {
    @include validation-state-focus($warning);
}

.has-error {
    @include validation-state-focus($danger);
}

.has-success {
    @include validation-state-focus($success);
}

.input-group > .select2-hidden-accessible {
    &:first-child
    + .select2-container--bootstrap4
    > .selection
    > .select2-selection,
    &:first-child
    + .select2-container--bootstrap4
    > .selection
    > .select2-selection.form-control {
        @include border-right-radius(0);
    }

    &:not(:first-child)
    + .select2-container--bootstrap4:not(:last-child)
    > .selection
    > .select2-selection,
    &:not(:first-child)
    + .select2-container--bootstrap4:not(:last-child)
    > .selection
    > .select2-selection.form-control {
        border-radius: 0;
    }

    &:not(:first-child):not(:last-child)
    + .select2-container--bootstrap4:last-child
    > .selection
    > .select2-selection,
    &:not(:first-child):not(:last-child)
    + .select2-container--bootstrap4:last-child
    > .selection
    > .select2-selection.form-control {
        @include border-left-radius(0);
    }
}

.input-group > .select2-container--bootstrap4 {
    display: table;
    table-layout: fixed;
    position: relative;
    z-index: 2;
    width: 100%;
    margin-bottom: 0;

    > .selection > .select2-selection.form-control {
        float: none;
    }

    &.select2-container--open,
    &.select2-container--focus {
        z-index: 3;
    }

    &,
    .input-group-btn,
    .input-group-btn .btn {
        vertical-align: top;
    }
}

.form-control.select2-hidden-accessible {
    position: absolute !important;
    width: 1px !important;
}

.form-inline .select2-container--bootstrap4 {
    @include media-breakpoint-up(sm) {
        display: inline-block;
    }
}
