@font-face {
    font-family: 'Maat-Legal';
    src: resolve('Maat-Legal/Maat-Legal.eot?b11zp7');
    src: resolve('Maat-Legal/Maat-Legal.eot?b11zp7#iefix')
        format('embedded-opentype'),
        resolve('Maat-Legal/Maat-Legal.ttf?b11zp7') format('truetype'),
        resolve('Maat-Legal/Maat-Legal.woff?b11zp7') format('woff'),
        resolve('Maat-Legal/Maat-Legal.svg?b11zp7#Maat-Legal') format('svg');
    font-weight: normal;
    font-style: normal;
}

.maat {
    &::before {
